.App {
  font-family: sans-serif;
  color: var(--tg-theme-text-color);
  background-color: var(--tg-theme-bg-color);
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.tab-content {
  flex: 1; /* Занимает всё доступное пространство */
  margin: 20px;
  overflow-y: auto; /* Добавляем прокрутку, если контент превышает высоту */
}

.sql-grid {
  height: 30%;
  width: 80%;
  top: 50%;
  left:auto;
  right: aut;
}

.button {
  color: var(--tg-theme-text-color);
  background-color: var(--tg-theme-bg-color);
}

.tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000; /* Убедимся, что она находится выше другого контента */
}